}

.user-menu__sign-up {
  display: inline-block;
  width: 102px;
  height: 40px;
  border: 1px solid #212832;
  border-radius: 5px;
  transition: all 0.5s ease;
  box-shadow: inset 0 0 0 0 #F1A501;
  text-align: center;
}

.user-menu__sign-up:active {
