}

.drop-down {
  display: flex;
  justify-content: flex-end;
  color: #212832;
  text-align: center;
  position: relative;
  cursor: pointer;
  transition: all 0.3s ease-out;
}

.drop-down__title {
  display: flex;
  flex-direction: row-reverse;
}

.drop-down__title::before {
  padding: 12.3px 6.15px;
  font-size: 12px;
  transition: all 0.1s ease-out;
}

.drop-down__list {
  position: absolute;
  top: 100%;
  right: 0;
  background: #fff;
  transition: all 0.2s ease-out;
  max-height: 0;
  overflow: hidden;
  margin: 0;
  padding: 0 9px;
  width: 84px;
}

.drop-down__link {
  display: block;
  color: inherit;
  padding: 6.5px 0px;
  transition: all 0.3s ease-out;
  border-top: 1px solid #e6e8ea;
}

.drop-down--active .drop-down__title:before {
  transform: rotate(180deg);
}

.drop-down--active .drop-down__list {
  box-shadow: 0px 30px 80px -20px rgba(0, 0, 0, 0.25);
  max-height: 400px;
}

.burger {
  position: relative;
}

.burger__menu,
.burger__content {
  position: relative;
  transition: 0.5s ease-in-out;
}

.burger__menu {
  display: flex;
  flex: 1 1 3.125rem;
  height: 3.125em;
  border-radius: 0% 0% 0% 100%;
  box-shadow: 0 0 0 0 #FFFAF0, 0 0 0 0 #FFFAF0;
  cursor: pointer;
}

.burger__content {
  margin: auto;
  width: 30px;
  height: 2px;
  display: block;
  background: #212832;
  transform-origin: center;
  z-index: 1;
}

.burger__content:after,
.burger__content:before {
  transition: 0.5s ease-in-out;
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  background: inherit;
}

.burger__content:before {
  top: -10px;
}

.burger__content:after {
  bottom: -10px;
}

#toggle {
  display: none;
}

@keyframes fadeIn {
  from {
    display: none;
    opacity: 0;
  }

  to {
    display: block;
    opacity: 1;
  }
}

#toggle:checked ~ .menu {
  top: 0;
  left: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  touch-action: none;
  background-color: #fff;
  z-index: 1;
  display: block;
  opacity: 1;
  animation: fadeIn 0.5s ease-in-out;
}

#toggle:checked ~ .menu .menu__list {
  flex-direction: column;
  line-height: 1.4705882353;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, 40%);
}

#toggle:checked ~ .burger .burger__menu {
  box-shadow: 0 0 0 100vw #fff, 0 0 0 100vh #fff;
  border-radius: 0;
  background: #fff;
}

#toggle:checked ~ .burger .burger__menu .burger__content {
  transform: rotate(45deg);
}

#toggle:checked ~ .burger .burger__menu .burger__content:after {
  transform: rotate(90deg);
  bottom: 0;
}

#toggle:checked ~ .burger .burger__menu .burger__content:before {
  transform: rotate(90deg);
  top: 0;
}

